<template>
  <div class="nav">
    <p class="nav-title">Blog</p>
    <el-menu mode="horizontal"
             background-color="#1B1C1D"
             active-text-color="#fff"
             :router="true"
             :uniqueOpened="true"
             :default-active="this.$route.path">
      <el-menu-item index="/main/home">首页</el-menu-item>
      <el-menu-item index="/main/label">分类</el-menu-item>
      <el-menu-item index="/main/label">标签</el-menu-item>
      <el-menu-item index="/main/label">关于</el-menu-item>
      <el-menu-item index="/main/label">日志</el-menu-item>
    </el-menu>

    <div class="empty-area"></div>

    <el-avatar class="profile-photo"
               src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
    </el-avatar>
    <p class="profile-username">{{ username }}</p>

    <div class="dropdown-wrapper">
      <el-dropdown @command="handleCommand">
        <i class="el-icon-arrow-down el-icon--right"></i>
        <template #dropdown>
          <el-dropdown-menu>
            <el-dropdown-item command="self-profile">我的档案</el-dropdown-item>
            <el-dropdown-item command="settings">设置</el-dropdown-item>
            <el-dropdown-item command="logout" divided>退出</el-dropdown-item>
          </el-dropdown-menu>
        </template>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import {requestDeleteOnlineUser, requestUserInfo} from "@/network/api";

export default {
  name: "Nav",
  computed:{
    ...mapGetters(['getUserInfo'])
  },
  created() {
    requestUserInfo().then(res => {
      if(res.data.code==="00000"){
        this.$store.dispatch("setUserInfo",res.data.data)
        this.username=res.data.data.username
      }
    })
  },
  data() {
    return {
      username: ""
    }
  },
  methods: {
    handleCommand(command) {
      this.$message.success(command);
      if (command === "logout") {
        let formData=new FormData()
        formData.append("id",this.getUserInfo.id)
        requestDeleteOnlineUser(formData).then(res=>{
          if(res.data.code==="00000"){
            window.sessionStorage.clear();
            this.$router.replace("/login");
          }
        })
      }
    }
  }
}
</script>

